 de recursos. Dejando a un lado los medios tradicionales, desde el entorno digital los distintos medios usados pueden ser: una página web, redes sociales, blogs con la combinación de elementos audiovisuales como videos, fotografías, transmisiones en vivo o podcasts; además de las diferentes plataformas que día a día se posicionan en diversos segmentos del mercado.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Las técnicas tradicionales que desarrolla una marca en la comunicación externa son adecuadas para utilizarse en el plan de medios adaptado al entorno digital (Soria, 2016), lo cual, al lograr posicionamiento, se traduce en el incremento de ventas y la fidelización de clientes. En el siguiente recurso de aprendizaje, se pueden explorar algunas de ellas.")]),_c('div',{staticClass:"fondo-morado-claro2 pt-4 mb-4"},[_c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-1"}),_c('div',{staticClass:"col-10"},[_c('TabsB',{staticClass:"color-primario"},[_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Publicidad","icono":require('@/assets/curso/tema2/img11.svg')}},[_c('div',{staticClass:"row d-flex align-items-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('div',{staticClass:"h5"},[_vm._v("Publicidad")]),_c('p',[_vm._v("Se implementa a través de anuncios de contenidos informativos que publiciten lo que desea una marca, para dar a conocer al público y orientar la adquisición del consumidor del bien o servicio.")])]),_c('div',{staticClass:"col-md-6 d-flex justify-content-center"},[_c('figure',{staticClass:"w-75"},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/img14.svg"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Relaciones públicas","icono":require('@/assets/curso/tema2/img12.svg')}},[_c('div',{staticClass:"row d-flex align-items-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('div',{staticClass:"h5"},[_vm._v("Relaciones públicas")]),_c('p',[_vm._v("Se establece a partir d